.container {
	max-width: 1290px;
	padding: 0 15px;
	margin: 0 auto;
}
.header {
	position: fixed;
	width: 100%;
	padding: 15px 0 8px;
	background: rgba(86,83,76,.53);
	z-index: 10;
	&__logo, &__nav {
		float: left;
	}
	&__logo {
		width: 21%;
		@media all and (max-width: 768px) {
			width: auto;
		}
	}
	&__social {
		width: 10%;
		float: right;
		padding-top: 10px;
	}
	&__nav {
		width: 69%;
		padding-top: 10px;
		@media all and (max-width: 1170px) {
			width: 79%;
			padding-left: 45px
		}
	}
}
.logo {
	display: inline-block;
}
.nav__item {
	font-family: 'Alegreya-Regular';
	font-size: 14px;
	float: left;
	position: relative;
	margin-right: 40px;
	text-transform: uppercase;
	letter-spacing: 2px;
	@media all and (max-width: 1170px) {
		margin-right: 30px;
	}
	&:after {
		content: "~";
		width: 7px;
		height: 3px;
		color: #ffffff;
		margin-left: 40px;
		@media all and (max-width: 1170px) {
			display: none;
		}
	}
	&:last-child:after {
		display: none;
	}
}
.nav__link {
	color: #ffffff;
	transition: .3s;
	&:hover, &:active {
		color: #dfc691;
	}
}
.social {
	float: right;
	&__item {
		float: left;
		margin-right: 22px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		height: 16px;
		display: inline-block;
		&:before {
			content: '';
			height: 100%;
			display: inline-block;
			vertical-align: middle;
		}
	}
	@media all and (max-width: 1170px) {
		display: none;

	}
}
