.header__nav {
	.nav__list {
		@media all and (max-width: 768px) {
			position: fixed;
			width: 100%;
			top: 0;
			left: 0;
			height: 100vh;
			background: rgba(86,83,76,0.9);
			transform: translateY(-200%);
			transition: .6s;
			padding: 25px 0 0 15px;
		}
		&.active {
			transform: translateY(0);
		}
	}
	.nav__item {
		@media all and (max-width: 768px) {
			float: none;
			width: 100%;
			margin-bottom: 25px;
		}
	}
}
.nav__burger {
	display: none;
	cursor: pointer;
	position: fixed;
	height: 13px;
	width: 25px;
	top: 30px;
	padding-top: 13px;
	right: 15px;
	@media all and (max-width: 768px) {
		display: block;
	}
}
.nav__burger-icon {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	width: 100%;
	height: 2px;
	background: #ffffff;
	&:before {
		content: '';
		position: absolute;
		width: 100%;
		height: 2px;
		background: #ffffff;
		left: 0;
		transition: .3s;
		top: -5px;
	}
	&:after {
		content: '';
		position: absolute;
		width: 100%;
		height: 2px;
		background: #ffffff;
		left: 0;
		transition: .3s;
		bottom: -5px;
	}
}
.nav__burger.active .nav__burger-icon {
	background: transparent;
}
.nav__burger.active .nav__burger-icon:before {
	content: '';
	transform: rotateZ(45deg);
	top: 0;
}
.nav__burger.active .nav__burger-icon:after {
	content: '';
	transform: rotateZ(-45deg);
	top: 0;
}