.footer {
	padding-bottom: 70px;
	background: #f3f1e9;
	&__nav {
		padding: 25px 0 20px;
		border-top: 1px solid #bfbbb7;
		border-bottom: 1px solid #bfbbb7;
		text-align: center;
		@media all and (max-width: 768px) {
			border-bottom: 0;
		}
	}
	.nav {
		display: inline-block;
	}
	.nav__item {
		margin-right: 35px;
		&:after {
			margin-left: 35px;
			color: #56534c;
		}
		@media all and (max-width: 480px) {
			float: none;
			width: 100%;
			margin: 15px 0;
		}
		&:last-child {
			margin-right: 0;
		}
	}
	.nav__link {
		color: #56534c;
		transition: .3s;
		&:hover, &:active {
			color:  #dfc691;
		}
	}
	&__logo {
		padding-top: 22px;
		display: block;
		text-align: center;
		@media all and (max-width: 992px) {
			display: none;
		}
	}
	&__social {
		margin-top: 25px;
		background: rgba(86,83,76,0.53);
		padding: 10px 0;
		text-align: center;
		display: none;
		@media all and (max-width: 1170px) {
			display: block;

		}
		.social {
			float: none;
			display: inline-block;
		}
	}
}

