.hero {
	padding: 260px 0 215px;
	background: url('../img/hero-bg.jpg') 50% 50% no-repeat;
	color: #ffffff;
	background-size: cover;
	position: relative;
	@media all and (max-width: 992px) {
		padding: 130px 0;
	}
	&__heading {
		font-size: 92px;
		font-family: 'PlayfairDisplay-Bold';
		padding-bottom: 60px;
		position: relative;
		margin-bottom: 65px;
		&:after {
			content: '';
			width: 190px;
			height: 11px;
			background: url('../img/hero-line.png') 50% 50% no-repeat;
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
		}
	}
	&__text {
		font-size: 24px;
		line-height: 35px;
		font-family: 'ProximaNova-Regular';
		letter-spacing: 2px;
		margin-bottom: 60px;
	}
	&__btn {
		display: inline-block;
		border: 1px dotted #9e9c93;
		background: rgba(86,83,76,.53);
		font-family: 'Alegreya-Bold';
		font-size: 14px;
		padding: 15px 55px;
		color: #ffffff;
		text-transform: uppercase;
		letter-spacing: 2px;
		transition: .3s;
		&:hover, &:active {
			background: #dfc691;
		}
	}
	&__elem-bottom {
		position: absolute;
		bottom: -62px;
		left: 50%;
		background: url('../img/elem-bg.png') no-repeat;
		padding: 13px;
		transform: translateX(-50%);
		&-inner {
			background: #ffffff;
			width: 80px;
			height: 80px;
			position: relative;
			border-radius: 50%;
			span {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
	}
}
