.about {
	padding: 100px 0;
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+43,fefefd+100 */
	background: #ffffff; /* Old browsers */
	background: -moz-linear-gradient(top, #ffffff 43%, #fefefd 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top, #ffffff 43%,#fefefd 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom, #ffffff 43%,#fefefd 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#fefefd',GradientType=0 ); /* IE6-9 */
	&__text {
		 font-family: 'ProximaNova-Regular';
		 font-size: 14px;
		 line-height: 24px;
		 letter-spacing: 2px;
		 max-width: 550px;
		 margin:  0 auto;
	}
	&__pic-item {
		margin-bottom: 20px;
		position: relative;
		// display: inline-block;
		width: 283px;
		height: 250px;
		overflow: hidden;
		@media all and (max-width: 1440px) {
			height: auto;
			width: auto;
		}
		&:before {
			content: '';
			display: block;
			width: 100%;
			padding-top: 84.44%;
		}
		img {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			margin: auto;
			object-fit: cover;
		}
	}
	&__pic {
		margin-top: 15px;
		[class^="col-"] {
			padding-left: 10px;
			padding-right: 10px;
		}
		&-border {
			width: calc(100% - 10px);
			height: calc(100% - 10px);
			border: 5px solid #ffffff;
			position: absolute;
			top: 5px;
			left: 5px;
			right: 5px;
			bottom: 5px;
			z-index: 5;
		}
		&-inner {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: 6;
			text-align: center;
			transform: translate(-50%, -50%);
			font-family: 'Alegreya-Regular';
			font-size: 30px;
			color: #c9b176;
			line-height: .9;
			padding: 95px 54px;
			background: url('../img/inner-bg.png') 50% 50% no-repeat;
			@media all and (max-width: 992px) {
				display: none;
			}
		}
	}
	&__info {
		@media all and (max-width: 992px) {
			margin-bottom: 50px;
		}
	}
}
.section-title {
	font-family: 'Alegreya-Bold';
	font-size: 48px;
	color: #56534c;
	letter-spacing: 2px;
	&_white {
		font-size: 60px;
		color: #ffffff;
		line-height: 1.1;
	}
}
.section-subtitle {
	font-family: 'Alegreya-Bold';
	font-size: 14px;
	text-transform: uppercase;
	color: #a47d5e;
	margin-bottom: 65px;
	letter-spacing: 2px;
	&_font {
		font-size: 18px;
	}
}
.stuff-quotes {
	&__author {
		font-family: 'Alegreya-Bold';
		font-size: 24px;
		color: #56534c;
		margin: 40px 0 45px;
	}
	&__text {
		font-family: 'Scriptina';
		font-size: 24px;
		line-height: 1.4;
		padding: 20px 0;
		color: #a47d5e;
		position: relative;
		max-width: 412px;
		margin: 0 auto;
		&:before {
			content: '';
			width: 23px;
			height: 14px;
			position: absolute;
			background: url('../img/quotes.png') 50% 50% no-repeat;
			top: 0;
			left: 0;
		}
		&:after {
			content: '';
			width: 23px;
			height: 14px;
			position: absolute;
			bottom: 2px;
			right: 0;
			background: url('../img/quotes.png') 50% 50% no-repeat;
			transform: rotate(180deg);
		}

	}
}
.stuff-member {
	margin-bottom: 20px;
	&__name, &__pic, &__surname {
		display: inline-block;
		vertical-align: middle;
	}
	&__name, &__surname {
		font-family: 'Alegreya-Regular';
		font-size: 24px;
		padding: 15px 14px;
		border-top: 1px solid #d7d5ca;
		border-bottom: 1px solid #d7d5ca;
		color: #56534c;
	}
	&__pic {
		border-radius: 50%;
		margin:  0 25px;
		position: relative;
		width: 100px;
		height: 100px;
		overflow: hidden;
		img {
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			margin: auto;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		@media all and (max-width: 480px) {
			display: block;
			margin: 0 auto;
		}
	}
}
.dish-card {
	padding: 140px 0 110px;
	background: url('../img/dish-card-bg.jpg') 50% 50% no-repeat;
	background-size: cover;
	&__wrapper {
		max-width: 965px;
		margin: 0 auto;
		position: relative;
		-webkit-box-shadow: -10px 15px 40px 0 rgba(0,0,0,0.3), 10px 0 40px 0 rgba(0,0,0,0.3);
		box-shadow: -10px 15px 40px 0 rgba(0,0,0,0.3), 10px 0 40px 0 rgba(0,0,0,0.3);
	}
	&__img, &__info {
		float: left;
		height: 512px;
		@media all and (max-width: 992px) {
			float: none;
		}
	}
	&__img {
		width: 44%;
		position: relative;
		overflow: hidden;
		// &:after {
		// 	content: '';
		// 	position: absolute;
		// 	bottom: -3px;
		// 	left: 0;
		// 	width: 0;
		// 	height: 0;
		// 	border-top: 50px solid #ffffff; 
		// 	border-left: 50px solid transparent;
		// 	@media all and (max-width: 992px) {
		// 		display: none;
		// 	}
		// }
		img {
			position: absolute;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			margin: auto;
			width: 100%;
			height: 100%;
			object-fit: cover; 
		}
		@media all and (max-width: 992px) {
			width: 100%;
			height: 412px;
		}
		// @media all and (max-width: 544px) {
		// 	height: 312px;
		// }
		&.compat-object-fit {
		   background-size: cover;
		   background-position: center center;
		   img {
		   	opacity: 0;
		   }
		}

	}
	&__stars {
		position: absolute;
		width: 22px;
		height: 150px;
		top: 50%;
		left: 28px;
		transform: translateY(-50%);
	}
	&__info {
		width: 56%;
		padding: 80px 60px 150px;
		background: #ffffff;
		position: relative;
		-webkit-box-shadow: -2px 0 0 0 rgba(0,0,0,0.1);
		box-shadow: -2px 0 0 0 rgba(0,0,0,0.1);
		&:after {
			content: '';
			position: absolute;
			right: 12px;
			bottom: 14px;
			height: 294px;
			width: 261px;
			background: url('../img/card-elem.png') 50% 50% no-repeat;
		}
		@media all and (max-width: 992px) {
			width: 100%;
			height: auto;
		}
		@media all and (max-width: 992px) {
			padding: 150px 25px 80px;
		}
	}
	&__descr {
		color: #56534c;
		font-family: 'ProximaNova-Regular';
		font-size: 14px;
		line-height: 24px;
		letter-spacing: 2px;
		height: 125px;
		overflow: auto;
		position: relative;
		z-index: 1;
		@media all and (max-width: 992px) {
			height: auto;
		}

	}
	&__content {
		display: none;
		position: relative;
		&.active {
			display: block;
			
		}
	}
	&__item {
		text-align: center;
	}
	&__link {
		display: inline-block;
		&.compat-object-fit {
			background-size: cover;
			background-position: center center;
			& img {
				opacity: 0;
			}
		}
	}
	&__pic, &__link {
		width: 90px;
		height: 90px;
		border-radius: 50%;
		object-fit: cover;
	}
	&__slider {
		padding: 0 50px;
		position: absolute;
		bottom: 42px;
		right: 60px;
		width: 100%;
		z-index: 3;
		max-width: 425px;
		.lSPager.lSpg {
			display: none;
			@media all and (max-width: 544px) {
				// display: block;
			}
		}
		@media all and (max-width: 992px) {
			left: 50%;
			right: 0;
			top: 440px;
			bottom: auto;
			transform: translateX(-50%);
		}
		.lSSlideOuter .lSPager.lSpg>li:hover a, .lSSlideOuter .lSPager.lSpg>li.active a {
			background: #56534c;
			margin-top: 20px;
			@media all and (max-width: 992px) {
				background: #dfc691;
			}
		}
	}
	.lSSlideWrapper {
		position: static;
	}
	.lSAction>.lSNext {
		right: 0;
	}
	.lSAction>.lSPrev {
		left: 0;
		
		transform: rotate(-180deg);
	}
	.lSAction>a {
		opacity: 1;
		background: url("../img/arrow.png") 50% 50% no-repeat;
		transition: .3s;
		&:hover {
			background: url("../img/arrow-hover.png") 50% 50% no-repeat;
		}
		
	}
	&__timing {
		position: absolute;
		top: -45px;
		right: -105px;
		@media all and (max-width: 1400px) {
			right: 0;
			top: -120px;
		}
	}
	&__elem {
		position: absolute;
		background: #ffffff;
		width: 50px;
		height: 50px;
		top: 50%;
		left: -25px;
		margin-top: -25px;
		border-radius: 50%;
		-webkit-box-shadow: -2px 0 0 0 rgba(0,0,0,0.1);
		box-shadow: -2px 0 0 0 rgba(0,0,0,0.1);
		&-inner {
			width: 1px;
			height: 16px;
			background: #d8bba3;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			&:before, &:after {
				content: '';
				width: 1px;
				height: 16px;
				background: #d8bba3;
				position: absolute;
			}
			&:before {
				left: -3px;
			}
			&:after {
				right: -3px;
			}
		}
		@media all and (max-width: 992px) {
			display: none;
		}
	}
}
