.meals-periods {
	background: url('../img/meals-periods-bg.jpg') 50% 50% no-repeat;
	background-size: cover;
	padding: 90px 0 70px;
	letter-spacing: 2px;
	position: relative;
	&:after {
		content: '';
		position: absolute;
		z-index: 6;
		width: 348px;
		height: 202px;
		background: url('../img/wheet.png') 50% 50% no-repeat;
		bottom: 15px;
		right: 0;
		@media all and (max-width: 1600px) {
			display: none;
		}
	}
	&__item {
		padding-top: 33px;
		position: relative;
		margin-bottom: 10px;
		&:before {
			content: '';
			position: absolute;
			background: url('../img/crown.png') 50% 50% no-repeat;
			width: 91px;
			height: 30px;
			top: 0;
			left: 50%;
			transform: translateX(-50%);
		}
		&:first-child {
			padding-top: 0;
			&:before {
				display: none;
			}
		}
		&-heading {
			font-family: 'Alegreya-Bold';
			font-size: 30px;
			margin-bottom: 10px;
			color: #ffffff;
		}
		&-text {
			color: #c2beba;
			font-size: 16px;
			font-family: 'ProximaNova-Regular';
			margin-bottom: 15px;
		}
		&-price {
			color: #dfc691;
			font-size: 36px;
			font-family: 'Alegreya-Regular';
		}
	}
	&__info {
		max-width: 440px;
		background: #f3f1e9;
		float: right;
		padding: 50px 35px 45px;
		position: relative;
		z-index: 5;
		margin-bottom: 65px;
		@media all and (max-width: 992px) {
			float: none;
			max-width: 100%;
		}
		&:before {
			content: '';
			position: absolute;
			width: calc(100% - 30px);
			height: 35px;
			left: 0;
			bottom: -65px;
			border-top: 30px solid  #f3f1e9; 
			border-left: 30px solid transparent;
		}
		&:after {
			content: '';
			position: absolute;
			bottom: -30px;
			left: 0;
			width: 0;
			height: 0;
			border-top: 30px solid #fafafa;
			border-left: 30px solid transparent;
			-webkit-box-shadow: 0 0 2px 2px rgba(0,0,0,0.1);
			box-shadow: 0 0 2px 2px rgba(0,0,0,0.1);
		}
		&-wrapper {
			position: relative;
			z-index: 7;
		}
		&-container {
			position: relative;
		}
		&-border {
			width: calc(100% - 20px);
			height: calc(100% + 10px);
			border: 1px dashed #9e9c93;
			position: absolute;
			top: 10px;
			left: 10px;
			right: 10px;
			bottom: 10px;
			overflow: hidden;
			-webkit-clip-path: polygon(0% 0%, 100% 0, 100% 100%, 5% 100%, 0 97%);
			clip-path: polygon(0% 0%, 100% 0, 100% 100%, 5% 100%, 0 97%);
		}
		// &-wrapper {
		// 	position: relative;
		// 	z-index: 5;
		// }
		&-elem {
			height: 1px;
			background: #c8c5c0;
			position: relative;
			margin: 30px 0;
			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: -6px;
				width: 11px;
				height: 11px;
				background: #f3f1e9;
				border-radius: 50%;
				border: 1px solid #c2beba;
			}
			&:after {
				content: '';
				position: absolute;
				right: 0;
				top: -6px;
				width: 11px;
				height: 11px;
				background: #f3f1e9;
				border-radius: 50%;
				border: 1px solid #c2beba;
			}
			&-icon {
				position: absolute;
				left: 50%;
				top: -30px;
				transform: translateX(-50%);
				width: 60px;
				height: 60px;
				border: 1px solid #c8c5c0;
				border-radius: 50%;
				background:  #f3f1e9;
				span {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
				}
			}
		}
		&-text {
			max-width: 360px;
			margin: 0 auto;
			letter-spacing: 1px;
		}
		&-heading {
			font-family: 'Alegreya-Bold';
			font-size: 44px;
			color: #56534c;
			margin: 100px 0 40px;
		}
		&-text {
			font-family: 'ProximaNova-Regular';
			font-size: 14px;
			color: #56534c;
			line-height: 30px;
			margin-bottom: 45px;
		}
		&-btn {
			display: inline-block;
			padding: 14px 58px;
			text-transform: uppercase;
			font-family: 'Alegreya-Bold';
			font-size: 18px;
			color: #a47d5e;
			border: 1px solid #9e9c93;
			transition: .3s;
			&:hover, &:active {
				background: #dfc691;
				color: #ffffff;
				border-color: transparent;
			}
		}
		&_second {
			position: absolute;
			right: 0;
			background: #dad7c9;
			width: 100%;
			height: 80%;
			z-index: 2;
			top: 30px;
			&:before {
				border-top: 30px solid  #dad7c9; 
				border-left: 30px solid transparent;
			}
			@media all and (max-width: 1440px) {
				display: none;
			}

		}
		&_third {
			position: absolute;
			right: -13px;
			background: #c4c2b7;
			width: 100%;
			height: 73%;
			top: 50px;
			z-index: 1;
			&:before {
				border-top: 30px solid #bfbcb1; 
				border-left: 30px solid transparent;
			}
			@media all and (max-width: 1440px) {
				display: none;
			}
		}
	}
}
