	&__ingredients-item {
		margin-bottom: 50px;
		letter-spacing: 1px;
	}
	&__ingredients-img{
		width: 100px;
		height: 100px;
		border-radius: 50%;
		display: inline-block;
		margin-bottom: 15px;
		-webkit-box-shadow: 0 4px 10px 1px rgba(0,0,0,0.1);
		box-shadow: 0 4px 10px 1px rgba(0,0,0,0.1);
		position: relative;
		overflow: hidden;
		img {
			@extend .dish-info__ingredients-img;
			object-fit: cover;
		}
		&.compat-object-fit {
			background-size: cover;
			background-position: center center;
			& img  {
				opacity: 0;
			}
		}
	}
	&__ingredients-title {
		font-family: 'Alegreya-Bold';
		font-size: 30px;
		color: #a47d5e;
		margin-bottom: 12px;
	}
	&__ingredients-text {
		font-family: 'ProximaNova-Regular';
		font-size: 14px;
		line-height: 20px;
		@media all and (max-width: 1440px) {
			br {
				display: none;
			}
		}
	}
	&__descr {
		margin: 85px 0 24px 98px;
		position: relative;
		box-shadow: -10px 15px 100px 0 rgba(0,0,0,0.1), 10px 0 100px 0 rgba(0,0,0,0.1);
		@media all and (max-width: 992px) {
			margin-left: 0;
			box-shadow: none;
		}
		&-img {
			border: 10px solid #ffffff;
			overflow: hidden;
			position: relative;
			&:before {
				content: '';
				display: block;
				width: 100%;
				padding-top: 65.1%;
			}
			img {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				height: 100%;
				width: 100%;
				object-fit: cover;
			}
		}
		&-elem {
			position: absolute;
			bottom: -20px;
			left: -22px;
			display: inline-block;
			padding: 16px 30px;
			font-family: 'Alegreya-Bold';
			font-size: 18px;
			color: #dfc691;
			background: #ffffff;
			border: 1px dashed #dfc691;
			transition: .3s;
			&:hover, &:active {
				background: #dfc691;
				color: #ffffff;
			}
			@media all and (max-width: 992px) {
				top: 0;
				left: 0;
				bottom: auto;
			}
		}
	}
	&__timing-item {
		@media all and (max-width: 992px) {
			margin-bottom: 30px;
		}
	}
}
@media all and (max-width: 544px) {
	// display: none;
	.dish-card .lSAction>.lSNext {
		right: 10px;
	}
	.dish-card .lSAction>.lSPrev {
		left: 10px;
	}
}